
.updatebtn{
    display: flex;
    justify-content: center;
    float: right;
}
.row-gap-15{
    row-gap: 15px;
}
.col-gap-15{
    column-gap: 15px;
    display: flex;

}
.register-cont{
    min-height: unset;
}
.PhoneInputInternationalIconPhone{
    fill: #d8d2d2a1 ;
}
.PhoneInputInput{
    outline: none;
    border: none;
}
.rdw-editor-main {
    min-height: 100%;
    overflow: auto;
    box-sizing: border-box;

}
.arbicJustfiy{
    direction: rtl;
    text-align: justify;
}
.PhoneInput--focus{
    color: #768192;
    background-color: #fff;
    border-color: #958bef;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgb(50 31 219 / 25%);
}
.input-group-text {

    min-width: 40px;
    place-content: center;
}
@media (max-width:768px) {
    .col-gap-15{
      flex-flow: wrap;

    }

}
